
import React from "react"
import { nanoid } from "nanoid";
export default function ExpenseList(props) {

    console.log(props.expenses);

    const expenseListHTML = props.expenses.map(item => {

        var now = new Date(item.createdAt.seconds * 1000);
        var todayUTC = new Date(Date.UTC(now.getFullYear(), now.getMonth(), now.getDate()));
        const timeStamp = todayUTC.toISOString().slice(0, 10); //.replace(/-/g, '');

        return <div key={nanoid()} >
            <button onClick={(event) => props.deleteExpense(event, item.id)}>x</button>
            {timeStamp} {item.expenseCost} - {item.expenseName}
            <br />
        </div>
    });

    return (
        <div>
            <h3>expense list : </h3>
            <br />
            {expenseListHTML}
        </div>
    )
}
