import React, { useRef, useState, useEffect } from "react"
import ExpenseInput from './ExpenseInput';
import ExpenseList from './ExpenseList';
import { useCollectionData } from 'react-firebase-hooks/firestore';
import 'firebase/compat/firestore';
import 'firebase/compat/auth';

function Expenses(props) {
    const firebase = props.firebase;
    const auth = firebase.auth();
    const firestore = firebase.firestore();
    const messagesRef = firestore.collection('messages');//.where('uid', '==', auth.currentUser.uid);
    console.log(auth.currentUser.uid);
    const query = messagesRef.where('uid', '==', auth.currentUser.uid)
    // .orderBy('createdAt')
    // .limit(25)
    const [expenses] = useCollectionData(query, { idField: 'id' });

    const dummy = useRef();

    function clearLocalStorage() {
        console.log("Clear local storage")
        localStorage.clear();
    }

    async function saveExpense(obj) {
        //local storage
        // console.log("saving expense " + obj);
        // const newExpense = obj;
        // setExpenses(prev => [...prev, obj])

        //firebase
        const { uid, photoURL } = auth.currentUser;
        obj.uid = uid;
        obj.createdAt = firebase.firestore.FieldValue.serverTimestamp();

        await messagesRef.add(obj)
    }

    function deleteExpense(event, noteId) {
        console.log(noteId)
        // setExpenses(prev => prev.filter(expense =>
        //     expense.id !== noteId
        // ))
    }

    return (

        <div>
            {/* Expenses
            <br />
            <button onClick={clearLocalStorage}>CLEAR LOCALSTORAGE</button> */}
            <ExpenseInput saveExpense={saveExpense} firestore={props.firebase.firestore()} />
            {expenses && <ExpenseList expenses={expenses} deleteExpense={deleteExpense} />}
        </div>
    )
}

export default Expenses